import React, { Component } from 'react'

class ToolBar extends Component {
  render() {

    var raz_button = this.props.empty ?
      undefined :
      <button id="new_game" onClick={this.props.new_game}>
        Remettre<br/>
        à Zéro
      </button>

    var autre_joueur = this.props.players ?
      <span>Ajouter un<br/>autre Joueur</span> :
      <span>Ajouter<br/>un Joueur</span>

    return (
      <tr id="ToolBar">
        <td width="100%" colSpan={this.props.cols+1}>
          <button id="new_player" onClick={this.props.show_new_player}>
            {autre_joueur}
          </button>
          {raz_button}
        </td>
      </tr>
    )
  }
}

export default ToolBar
